<template>
  <div id="app">
    <h1 class="title" >To  Do  List</h1>
    <div class="enter">
      <input type="text" v-model="newItem" @keyup.enter="addNew" v-focus class="newItem" >
      <input type="button"  value="撤回" @click="back">
    </div>
    <div class="wrap">
    <ul class="items">
      <transition-group name="fade">
      <li class="item" v-for="item in itemList "  v-show="item.show"  v-bind:key="item">
        <input   v-model='item.lable' @keyup.enter="modify(item)" class="text" @focus="change(item)" @blur="changeAgain(item)">
      <transition-group name="fade">
        <input type="button" value="删除" @click="_delete(item)" class="button" v-show="item.ffoucs" v-bind:key="1" >
        <input type="button" value="详情" @click="_detail(item)" v-show="item.ffoucs" class="button"v-bind:key="2" >
      </transition-group >
      </li>
    </transition-group>
    </ul>
    </div>
  </div>
</template>

<script>
import store from  './store'
export default {
  data(){
    return{
      count:0,//用于计数删除的条数，以便在没有可撤回的数据时给出相应的提示
      newItem:'',//最新键入的值
      deleList:[],//被删元素的集合数组
      itemList:store.fetch()?store.fetch():[]//这里也可以简写成store.fetch||[]   初始化读取的数据
    }
  },
  directives:{ //用于自动聚焦
    focus:
    {
      inserted(el)
      {
        el.focus()

      }
    }

    },

  methods:{
    change(item){
        item.ffoucs=1;
    },
    changeAgain(item){
      item.ffoucs=0;
    },//这里监听了focus事件，用来显现或者隐藏
    _detail(item){
      window.alert(item.lable)
    },
    back(){
      if(this.count!==0){
      let length=this.deleList.length;
      this.deleList[length-1].show=true;
      this.deleList.pop();
      this.count-=1;
      store.save(this.itemList);
    }else{
        window.alert("没有可撤回的数据")
    }
  },//撤销模块，为此专门建立了一个数组来存放被删除的数据

    _delete(item){
        item.show=!item.show;
        this.deleList.push(item);
        store.save(this.itemList);
        this.count+=1;
    },

    addNew(){
      this.itemList.push({lable:this.newItem,show:true,ffoucs:0});
      store.save(this.itemList);
      this.newItem='';
    },
    modify(item,index){
      this.itemList[index]=item;
      store.save(this.itemList);

    }//调用了自带的index属性

  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">

  .title
    text-align:center
    margin-left:-45px

  .enter
    text-align:center
    .newItem
      border-radius:5px
      border:1px solid #CCCCCC
      height:25px
  .wrap
    text-align:center
    .items
      list-style:decimal
      display:inline-block
      margin-left:90px
      width:490px
      .item
        height:40px
        margin-bottom:5px
        text-align:left
        transform:translate3d(0,0,0)
        .button
         &.fade-enter-active,&.fade-leave-active
           transition: opacity  0.5s
         &.fade-enter,&.fade-leave-active
           opacity:0
        .text
         width:70%
         height:70%
         border-radius:5px
         border:1px solid #CCCCCC
         white-space:nowrap
         overflow:hidden
         text-overflow:ellipsis
        &.fade-enter-active
         animation: bounce-in .5s
        &.fade-leave-active
         animation: bounce-out .5s
        @keyframes bounce-in{
            0% {
              transform: scale(0)
            }
            50% {
              transform: scale(1.5)
            }
            100% {
              transform: scale(1)
            }
          }
        @keyframes bounce-out {
            0% {
              transform: scale(1)
            }
            50% {
              transform: scale(1.5)
            }
            100% {
              transform: scale(0)
            }
          }


</style>



